html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strong,
sub,
sup,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html {
  color: #0f1422;
  font-family: proxima-nova, sans-serif;
  font-style: normal;
  font-weight: 100;
  font-size: 10px;
}
body {
  color: #0f1422;
  font-size: 1.6rem;
}
/* eslint-disable prettier/prettier */
/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
/* Checked/Unchecked Disabled Radio,
 * Checked/Unchecked Disabled Checkbox,
 * Disabled Select
 */
/* eslint-enable prettier/prettier */
.ce-radio {
  user-select: none;
}
.ce-radio input {
  opacity: 0;
}
.ce-radio input + label {
  cursor: pointer;
  display: inline-block;
  height: 2rem;
  line-height: 2rem;
  padding: 0.8rem 4.4rem;
}
.ce-radio input + label::before {
  background-color: #b2b2b2;
  border: 0.75rem solid #fff;
  border-radius: 50%;
  box-sizing: border-box;
  content: '';
  height: 3.6rem;
  left: 0;
  margin: -0.8rem;
  margin-left: 2rem;
  position: absolute;
  width: 3.6rem;
}
.ce-radio input + label::after {
  background-color: #fbfbfb;
  border-radius: 50%;
  box-sizing: border-box;
  content: '';
  height: 2rem;
  left: 0;
  margin-left: 2.8rem;
  position: absolute;
  width: 2rem;
  box-shadow: -0.5px 0 #b2b2b2, 0.5px 0 #b2b2b2, 0 -0.5px #b2b2b2, 0 0.5px #b2b2b2;
}
.ce-radio input + label:hover::before {
  background-color: #e5e5e5;
  border: 0;
}
.ce-radio input + label:hover::after {
  box-shadow: none;
}
.ce-radio input + label:active::before {
  background-color: #b3b3b3;
  border: 0;
}
.ce-radio input + label:active::after {
  box-shadow: none;
}
.ce-radio input + label:focus::before {
  background-color: #ccc;
  border: 0;
}
.ce-radio input + label:focus::after {
  box-shadow: none;
}
.ce-radio input:checked + label::before {
  background-color: #192344;
}
.ce-radio input:checked + label::after {
  background-color: #192344;
  border: 0.5rem solid #fff;
}
.ce-radio input:checked + label:hover::before {
  background-color: #e6f7ff;
}
.ce-radio input:checked + label:active::before {
  background-color: #b2e7ff;
}
.ce-radio input:checked + label:focus::before {
  background-color: #ccefff;
}
.ce-radio + .ce-radio {
  margin-top: 0.4rem;
}
