.ce-toggle-button-group {
  border-radius: 3.5rem;
  border-width: 0;
  display: inline-block;
}
.ce-toggle-button-group legend {
  display: none;
}
.ce-toggle-button-group.ce-toggle-button-group--small .ce-toggle-button label {
  padding: 0.9rem 1.33rem 0.5rem 1.33rem;
}
.ce-toggle-button-group.ce-toggle-button-group--small .ce-toggle-button:first-of-type label {
  padding-left: 2.33rem;
}
.ce-toggle-button-group.ce-toggle-button-group--small .ce-toggle-button:last-of-type label {
  padding-right: 2.33rem;
}
.ce-toggle-button-group.ce-toggle-button-group--large .ce-toggle-button label {
  padding: 1.4rem 2rem 1.1rem 2rem;
}
.ce-toggle-button-group.ce-toggle-button-group--large .ce-toggle-button:first-of-type label {
  padding-left: 3rem;
}
.ce-toggle-button-group.ce-toggle-button-group--large .ce-toggle-button:last-of-type label {
  padding-right: 3rem;
}
.ce-toggle-button-group.ce-toggle-button-group--jumbo .ce-toggle-button label {
