html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strong,
sub,
sup,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html {
  color: #0f1422;
  font-family: proxima-nova, sans-serif;
  font-style: normal;
  font-weight: 100;
  font-size: 10px;
}
body {
  color: #0f1422;
  font-size: 1.6rem;
}
/* eslint-disable prettier/prettier */
/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
/* Checked/Unchecked Disabled Radio,
 * Checked/Unchecked Disabled Checkbox,
 * Disabled Select
 */
/* eslint-enable prettier/prettier */
.ce-radio-group {
  border: 0.3rem solid #fff;
  border-radius: 0.5rem;
  clear: both;
  margin-bottom: 0.7rem;
}
.ce-radio-group legend {
  margin-bottom: 2.2rem;
  margin-left: 2.2rem;
  padding: 0 0.3rem;
}
.ce-radio-group span {
  background-color: #fff;
  display: block;
  float: right;
  height: 1.6rem;
  margin-bottom: -1rem;
  margin-right: 2.2rem;
  margin-top: 2.2rem;
  padding: 0 0.3rem;
}
.ce-radio-group.ce-radio-group--disabled {
  pointer-events: none;
}
.ce-radio-group.ce-radio-group--disabled input + label::before {
  background-color: #dbdbdb;
}
.ce-radio-group.ce-radio-group--disabled input:checked + label::before {
  background-color: #aeaeae;
}
.ce-radio-group.ce-radio-group--disabled input:checked + label::after {
  background-color: #aeaeae;
}
.ce-radio-group.ce-radio-group--error {
  border: 0.3rem solid #e5004b;
  color: #e5004b;
}
.ce-radio-group.ce-radio-group--error input:checked + label::before {
  background-color: #e5004b;
}
.ce-radio-group.ce-radio-group--error input:checked + label::after {
  background-color: #e5004b;
  border: 0.5rem solid #fff;
  box-shadow: -0.5px 0 #e5004b, 0.5px 0 #e5004b, 0 -0.5px #e5004b, 0 0.5px #e5004b;
}
.ce-radio-group.ce-radio-group--error input:checked + label:hover::before {
  background-color: #ffe5ed;
}
.ce-radio-group.ce-radio-group--error input:checked + label:hover::after {
  box-shadow: none;
}
.ce-radio-group.ce-radio-group--error input:checked + label:active::before {
  background-color: #ffb3cc;
}
.ce-radio-group.ce-radio-group--error input:checked + label:active::after {
  box-shadow: none;
}
.ce-radio-group.ce-radio-group--error input:checked + label:focus::before {
  background-color: #ffccdc;
}
.ce-radio-group.ce-radio-group--error input:checked + label:focus::after {
  box-shadow: none;
}
