html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strong,
sub,
sup,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html {
  color: #0f1422;
  font-family: proxima-nova, sans-serif;
  font-style: normal;
  font-weight: 100;
  font-size: 10px;
}
body {
  color: #0f1422;
  font-size: 1.6rem;
}
/* eslint-disable prettier/prettier */
/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
/* Checked/Unchecked Disabled Radio,
 * Checked/Unchecked Disabled Checkbox,
 * Disabled Select
 */
/* eslint-enable prettier/prettier */
.ce-icon.ce-icon__product {
  align-items: center;
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
}
.ce-icon.ce-icon__product.ce-icon--small {
  height: 5rem;
  width: 5rem;
}
.ce-icon.ce-icon__product.ce-icon--small svg {
  height: calc(5rem - 40%);
  margin: 0 auto;
  width: calc(5rem - 40%);
}
.ce-icon.ce-icon__product.ce-icon--large {
  height: 7.5rem;
  width: 7.5rem;
}
.ce-icon.ce-icon__product.ce-icon--large svg {
  height: calc(7.5rem - 40%);
  margin: 0 auto;
  width: calc(7.5rem - 40%);
}
.ce-icon.ce-icon__product.ce-icon--jumbo {
  height: 10rem;
  width: 10rem;
}
.ce-icon.ce-icon__product.ce-icon--jumbo svg {
  height: calc(10rem - 40%);
  margin: 0 auto;
  width: calc(10rem - 40%);
}
.ce-icon.ce-icon__product .ce-icon--white {
  fill: #fff;
}
.ce-icon.ce-icon__secondary {
  align-items: center;
  border-radius: 50%;
  display: flex;
  justify-content: center;
}
.ce-icon.ce-icon__secondary.ce-icon--small {
  height: 3rem;
  width: 3rem;
}
.ce-icon.ce-icon__secondary.ce-icon--small svg {
  height: calc(3rem - 40%);
  width: calc(3rem - 40%);
}
.ce-icon.ce-icon__secondary.ce-icon--small span {
  color: white;
  font-size: calc(3rem - 90%);
  font-weight: bold;
}
.ce-icon.ce-icon__secondary.ce-icon--large {
  height: 3.6rem;
  width: 3.6rem;
}
.ce-icon.ce-icon__secondary.ce-icon--large svg {
  height: calc(3.6rem - 40%);
  width: calc(3.6rem - 40%);
}
.ce-icon.ce-icon__secondary.ce-icon--large span {
  color: white;
  font-size: calc(3.6rem - 90%);
  font-weight: bold;
}
.ce-icon.ce-icon__secondary.ce-icon--jumbo {
  height: 5rem;
  width: 5rem;
}
.ce-icon.ce-icon__secondary.ce-icon--jumbo svg {
  height: calc(5rem - 40%);
  width: calc(5rem - 40%);
}
.ce-icon.ce-icon__secondary.ce-icon--jumbo span {
  color: white;
  font-size: calc(5rem - 90%);
  font-weight: bold;
}
.ce-icon.ce-icon__secondary.ce-icon--primary {
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
}
.ce-icon.ce-icon__secondary.ce-icon--secondary {
  background: linear-gradient(317.58deg, #62248d 0%, #c4356e 100%);
}
.ce-icon.ce-icon__secondary .ce-icon--white {
  stroke: #fff;
}
.ce-icon.ce-icon__system {
  stroke: #72828d;
}
.ce-icon.ce-icon__system.ce-icon--small {
  height: 1.2rem;
  width: 1.2rem;
}
.ce-icon.ce-icon__system.ce-icon--small svg {
  height: 1.2rem;
  width: 1.2rem;
}
.ce-icon.ce-icon__system.ce-icon--large {
  height: 1.6rem;
  width: 1.6rem;
}
.ce-icon.ce-icon__system.ce-icon--large svg {
  height: 1.6rem;
  width: 1.6rem;
}
.ce-icon.ce-icon__system.ce-icon--jumbo {
  height: 1.6rem;
  width: 1.6rem;
}
.ce-icon.ce-icon__system.ce-icon--jumbo svg {
  height: 1.6rem;
  width: 1.6rem;
}
.ce-icon.ce-icon__system .ce-icon--black {
  stroke: #000;
}
.ce-icon.ce-icon__system .ce-icon--blue {
  stroke: #00b0ff;
}
.ce-icon.ce-icon__system .ce-icon--green {
  stroke: #acd84f;
}
.ce-icon.ce-icon__system .ce-icon--orange {
  stroke: #fca635;
}
.ce-icon.ce-icon__system .ce-icon--purple {
  stroke: #7838a8;
}
.ce-icon.ce-icon__system .ce-icon--red {
  stroke: #ff0054;
}
.ce-icon.ce-icon__system .ce-icon--white {
  stroke: #fff;
}
