.ce-toggle-button-group--disabled .ce-toggle-button:hover {
  background: transparent;
}
.ce-toggle-button-group--disabled .ce-toggle-button:hover label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--disabled .ce-toggle-button:active {
  background: #ccc;
}
.ce-toggle-button-group--disabled .ce-toggle-button:active label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--disabled .ce-toggle-button.ce-toggle-button--checked {
  background: transparent;
}
.ce-toggle-button-group--disabled .ce-toggle-button.ce-toggle-button--checked label .ce-icon {
  stroke: #fff;
}

