dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html {
  color: #0f1422;
  font-family: proxima-nova, sans-serif;
  font-style: normal;
  font-weight: 100;
  font-size: 10px;
}
body {
  color: #0f1422;
  font-size: 1.6rem;
}
/* eslint-disable prettier/prettier */
/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
/* Checked/Unchecked Disabled Radio,
 * Checked/Unchecked Disabled Checkbox,
 * Disabled Select
 */
/* eslint-enable prettier/prettier */
button {
  margin: 0;
  padding: 0;
}
.ce-button {
  border-radius: 3.5rem;
  border-width: 0;
  display: flex;
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 900;
  justify-content: center;
  text-transform: uppercase;
}
@media screen and (max-width: 768px) {
  .ce-button {
    width: 100%;
  }
}
.ce-button .ce-button__label {
  flex-grow: 1;
}
.ce-button.ce-button--small .ce-button__label {
  border-radius: 3.5rem;
  display: inline-block;
  font-size: 1.2rem;
  margin: 0.2rem;
  padding: 0.85rem 3.8rem;
}
.ce-button.ce-button--large .ce-button__label {
  border-radius: 3.5rem;
  display: inline-block;
  font-size: 1.6rem;
  margin: 0.2rem;
  padding: 1.3rem 5.8rem;
}
.ce-button.ce-button--jumbo .ce-button__label {
  border-radius: 3.5rem;
  display: inline-block;
  font-size: 1.6rem;
  margin: 0.2rem;
  padding: 2.3rem 5.8rem;
}
.ce-button:focus {
  box-shadow: 0 0 0 5px #b2e7ff;
  outline: none;
}
.ce-button--outline.ce-button--small .ce-button__label {
  background: #fff;
}
.ce-button--outline.ce-button--large .ce-button__label {
  background: #fff;
}
.ce-button--outline.ce-button--jumbo .ce-button__label {
  background: #fff;
}
.ce-button--text.ce-button--small .ce-button__label {
  margin: 0;
  padding: 1.05rem 4rem;
}
.ce-button--text.ce-button--large .ce-button__label {
  margin: 0;
  padding: 1.5rem 6rem;
}
.ce-button--text.ce-button--jumbo .ce-button__label {
  margin: 0;
  padding: 2.5rem 6rem;
}
.ce-button--text:focus {
  box-shadow: none;
}
.ce-button--text:focus .ce-button__label {
  text-decoration: underline;
}
